	module.exports = {"data":{"nodePost":{"title":"No More Guns Please","link":"http://www.chicagotribune.com/news/local/breaking/ct-met-shooting-hyatt-hotel-20180216-story.html","body":{"value":"<blockquote>\r\n<p>Theresa Nicholls was eager to get back home as she packed her bags at the Hyatt Regency Chicago on Friday.</p>\r\n<p>Her weeklong conference of school psychologists had been rocked by the mass shooting at a Florida high school. A day earlier, a Chicago police officer had been shot and killed in the Loop, just blocks from where she and her colleagues were staying.</p>\r\n<p>As she was getting ready to check out, a friend texted her: There had just been a shooting in the lobby of her hotel.</p>\r\n<p>“What the hell is going on?\" she thought. \"What is happening? It’s like the world is falling apart around us.”</p>\r\n</blockquote>\r\n<p>My wife was at this conference. There are a lot of reasons we need to do something about America's access to guns.  Let's add this one to the list.</p>"},"created":1519066208}},"pathContext":{"slug":"/link/no-more-guns-please/"}}

